@import "normalaze.scss";
@import "slick-theme.scss";
@import "slick.scss";

$black:#061e37;
$gray:#b0bec5;

@mixin title {
    text-align: center;
    &__up{
        font-size: 45px;
        color: $black;
    }
    &__sub{
        font-size: 25px;
        color: $gray;
        padding-top: 20px;
    }
    p{
        color: $gray;
        font-size: 15px;
        max-width: 557px;
        margin: 0 auto;
        padding-top: 26px;
    }
}

@mixin button {
   a{
    text-decoration: none;
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 2px;
    box-shadow: 0 5px 5px 0px #061e37;
    transition: 0.2s ease-in-out;
}
a:hover{
    transition: 0.2s ease-in-out;
}
}


h1,h2,h3,h4,p{
    padding: 0;
    margin: 0;
}
h1{
    font-weight:800;
    font-size: 55px;
}

h2{
    font-size: 45px;
    font-weight: 600;

}

h3{
    font-weight: 600;
    font-size: 20px;
}

p{
    font-weight: 400;
}
h4{
    font-size: 15px;
    font-weight: 700;

}

body{
    font-family: 'Raleway', sans-serif;
}

.container{
    max-width: 1440px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}

.page-header-bg{
   background: url("../img/headbg.png") no-repeat center;
   background-size: cover;
   padding-bottom: 190px;
}

.page-header{
    padding-top: 50px;
    padding-bottom: 157px;
    &__navigation{
        display: flex;
        justify-content: space-between;
        align-items: center;
      position: relative;

        &__menu{
            background: transparent;
            position: relative;
            height: 21px;
            width: 30px;
            display: none;
            @media (max-width:1500px) {
                display: block;
            }
            span,span::before,span::after{
                background: #fff;
                height: 2px;
                width: 30px;
                content: "";
                position: absolute;
                top: 50%;
                display: block;
            }
            span::before{
               transform: translateY(-10px);
                   transition: 0.2s ease-in-out;
                
            }
            span::after{
                transform: translateY(8px);
                    transition: 0.2s ease-in-out;
               
            }
        }
        &__logo{
            display: flex;

            a{
                margin-left: 10px;
                font-weight: 800;
                color: #fff;
                font-size: 15px;
                text-decoration: none;
                text-transform: uppercase;
                span{
                    display: block;
                    font-size: 15px;
                    font-weight: 400;
                }
            }
        }
        &__links{
            flex-basis: 48%;
            @media (max-width:1500px) {
             right: 0;
             top: 45px;
                position: absolute;
                transform: scale(0);
            }
            ul{
                display: flex;
                @media (max-width:1500px) {
                    flex-direction: column;
                }
                li{
                    list-style-type: none;
                    margin-left: 47px;
                    
                    a{
                        
              text-decoration: none;
                font-weight: 600;
                color: #fff;
                font-size: 15px;
                transition: 0.2s ease-in-out; 
                    }
                    a:hover{
                        color: $black;
                        transition: 0.2s ease-in-out; 
                    }
                }
               
            }
        }
        &__signup{
            @media (max-width:1500px) {
                right: 0;
                position: absolute;
                top: 157px;
                transform: scale(0);
            }
            font-size: 12px;
            text-decoration: none;
        color: #fff;
        transition: 0.2s ease-in-out; 
            font-weight: 400;
            background: transparent;
            padding: 5px 18px;
            border: 1px solid #fff;
            border-radius: 2px;
        }
        &__signup:hover{
            color: $black;
        }
    }
    &__content{
        display: flex;
        justify-content: space-between;
        @media (max-width:1000px) {
            justify-content: center;
            flex-direction: column;
            text-align: center;
        }
        &__text{
            flex-basis: 40%;
          
            h1{
                color: #fff;
                text-transform: uppercase;
                padding-bottom:17px;
            }
            p{
                font-size: 18px;
                color: #fff;
                line-height: 1.9;
            }
            &__button{
                padding-top: 40px;
                display: flex;
                @media (max-width:1000px) {
                    justify-content: center;
                }
                    @include button();

            
               &__free{
                   margin-left: 28px;
                   background: #fff;
                   padding:22px 32px;
                    color: $black;
               }
               &__buy{
                background: $black;
                padding:22px 32px;
                color: #fff;
               }


            }
        }
        &__video{ 
            flex-basis: 40%;
            @media (max-width:1000px) {
                margin: 0 auto;
                margin-top: 50px;
            }
        &__block{
            background: url("../img/videobg.png") no-repeat center;
            background-size: cover;
            width: 600px;
            height: 320px;
          text-align: center;
          @media (max-width:1500px) {
            width: 400px;
        }
        @media (max-width:440px) {
            width: 250px;
        }
            &__play{
                background: $black;
                width: 60px;
                height: 60px;
                margin: 0 auto;
                display: inline-block;
                margin-top: 115px;
                span{
                    margin-top: 18px;
                    margin-left: 22px;
                    display: block;
                    border: 12px solid transparent;
                    border-left: 16px solid #fff;

                }
            }
            p{
                padding-top: 20px;
                color: $black;
                font-size: 12px;
                text-transform: uppercase;
                font-weight: 700;
            }
        }
    }
    }
}



.page-easy{
    padding-top: 100px;
    padding-bottom: 100px;
    &__content{
        @include title();
    }
    &__block{
        display: flex;
        justify-content: space-between;
        text-align: center;
        padding-top: 93px;
        padding-bottom: 80px;
        @media (max-width:1000px) {
            display: block;
        }
        &__item{
            flex-basis: 22%;
            h3{
                padding-bottom: 20px;
                padding-top: 38px;
                font-size: 20px;
            }
        }
    }
        &__button{
            @include button();
            text-align: center;
            a{
                background: #1de9b6;
                color: #fff;
                padding: 21px 29px;
            }
            a:hover{
                background: #80d8ff;
            }
        }
    
}

.page-stat{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(280px,395px));
    grid-auto-rows: 152px;
    justify-content: center;
    &__item{
        display: flex;
        align-items: center;
        justify-content: center;
        &__one{
            background: #80d8ff;
            i{
                color:  #80d8ff;
            }
        }
        &__two{
            background: #82b1ff;
            i{
                color: #82b1ff;
            }
        }
        &__three{
            background: #b388ff;
            i{
                color: #b388ff;
            }
        }
        &__four{
            background: #ea80fc;
            i{
                color: #ea80fc;
            }
        }
        &__round{
            background: #fff;
            height: 62px;
            width: 62px;
            border-radius: 100%;
           margin-right: 23px;
            i{
                display: flex;
                justify-content: center;
                margin-top: 16px;
                font-size: 30px;
            }
        }
        h3{
            color: #fff;
            span{
                display: block;
            }
        }
    }
}

.page-services{
    padding-top: 99px;
    padding-bottom: 99px;
    &__content{
        @include title();
        padding-bottom: 45px;
    
    }
    &__block{
        display: grid;
        justify-content: center;
        grid-template-columns: minmax(280px,803px);
        grid-auto-rows: max-content;
        &__item{
            border-radius: 5px;
            box-shadow: 0px 1px 6px 1px $black;
            margin-bottom: 18px;
            &__container{
                padding: 21px 25px;
            }
            &__header{
                display: flex;
                justify-content: space-between;
                align-items: center;
                
              
                &__text{
                    display: flex;
                    align-items: center;

                    h4{
                        margin-left: 25px;
                        text-transform: uppercase;
                    }
                }
                &__dropdown{
                    text-decoration: none;
                    color: $black;
                    font-size: 25px;
                   transition: 0.3s ease-in-out;
                }
            }
            &__body{
                display: flex;
                justify-content: space-between;
               display: none;
               @media (max-width:770px) {
                flex-direction: column;
                justify-content: center;
                text-align: center;
            }
                img{
                    height: 155px;
                    width: 250px;
                    object-fit: cover;
                    @media (max-width:770px) {
                        margin: 0 auto;
                    }
                }
                &__text{
                    flex-basis: 63%;
                    &__one{
                        padding-bottom: 40px;
                        @media (max-width:770px) {
                            padding-top: 20px;
                        }
                    }
                }
            }
        }
    }
}

.customersbg{
    background: url("../img/customersbg.jpg") no-repeat center;
    background-size: cover;

}

.page-customers{
    padding-top: 40px;
    padding-bottom: 40px;
    &__block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media  (max-width:880px) {
            flex-direction: column;
