.contactbg{
    background: #fafafa;
}
.page-contact{
 padding-top: 50px;
 padding-bottom: 100px;
    &__content{
        @include title();
        color: $black;
        padding-bottom: 50px;
      
    }
    &__block{
       text-align: center;
       max-width: 600px;
       margin: 0 auto;
        input{
            box-sizing: border-box;
            display: inline-block;
            padding: 18px 19px;
            border-radius: 5px;
            border: 1px solid $black;
            outline: none;
            width: 100%;
            max-width: 279;
            margin-bottom: 20px;    
        }
        input:nth-child(2){
            margin-left: 37px;
            @media  (max-width:640px) {
                margin-left: 0;
                 }
        }
        input:nth-child(4){
            margin-left: 37px;
            @media  (max-width:640px) {
                margin-left: 0;
                 }
        }
        textarea{
            resize: none;
            outline: none;
            border-radius: 5px;
            border: 1px solid $black;
            width: 100%;
            padding: 18px 19px;
        }
        &__buttons{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-top: 20px;
            @media  (max-width:440px) {
                flex-direction: column;
                justify-content: center;
                 }
            ul{
                padding: 0;
                li{
                    list-style-type: none;
                    padding-left: 20px;
                    input{
                        cursor: pointer;
                        position: relative;
                        padding: 0;
                        display: none;
                    }
                    label{
                        position: relative;
                        display: inline-block;
                        cursor: pointer;
                        padding-left: 9px;
                        font-weight: 500;
                        font-size: 15px;
                        color: $black;
                    }
                    label::before{
                        position: absolute;
                        left: -18px;
                        top: 0px;
                        background: #fff;
                        content: "";
                        width: 16px;
                        height: 16px;
                        border-radius: 1px;
                        border: 1px solid #333;
                        display: inline-block;
                    }
                }
            }
            button{
                @include button();
                outline: none;
                border: none;
                cursor: pointer;
                padding: 19px 45px;
                background: #1de9b6;
                color: #fff;
                border-radius: 5px;
                text-transform: uppercase;
            }
        }
    }
}

