 &__social{
    text-align: center; 
    a{
        text-decoration: none;
        color: #90a4ae;
        margin-left: 27px;
    }
}
}
}


.slick-slider {

    height: 343px;
}

.slick-next {
    right: 0; 
}
.slick-prev{
    left: 0;
}

/* animation */

.show{
    display: block;
    animation: show 0.3s ease-in-out;
    justify-content: space-between;
    display: flex;
}
.showicon{
    transform: rotate(180deg);
    transition: 0.3s ease-in-out;
}

@keyframes show {
    0%{
        transform: translateY(-68px);
    }
    50%{
        transform: translateY(-20px);
    }
    100%{
        transform: translateY(0);
    }
}
.showmenu{
    animation: showmenu  .3s ease-in-out;
    transform: scale(1);
}
@keyframes showmenu {
    0%{
        transform: translatey(-70px);
    }
    50%{
        transform: translatey(-20px);
    }
    100%{
        transform: translatey(0px);
    }
}
.menu-active{
    span{
      height: 0;
    }
    span::before{
      
            transform: rotate(-45deg);
     }
     span::after{
      
         transform: rotate(45deg);
     }
}
.addpadding{
    padding-bottom: 25px;
}
.padding{
    padding: 0;
}
